var DataSurgeryStore = (function () {
    function DataSurgeryStore() {
        this.dates = [];
        this.metrics = new SurgeryMetrics();
        this.surgeries = [];
        this.futureSurgeries = [];
        this.pastSurgeries = [];
        this.todaySurgeries = [];
        this.groupedSurgeries = [];
    }
    DataSurgeryStore.prototype.refreshData = function () {
        var _this = this;
        console.group("Refresh Surgery Data");
        //sort by date
        // try {
        //   this.data.sort((a: PulseViewModel, b: PulseViewModel) => {
        //     return new Date(a.term).getDate() - new Date(b.term).getDate();
        //   });
        // } catch (error) {
        //   console.error('Sort Error"');
        // }
        this.pastSurgeries = [];
        this.futureSurgeries = [];
        this.surgeries = [];
        this.todaySurgeries = [];
        this.metrics = new SurgeryMetrics();
        this.data.sort(function (a, b) {
            try {
                if (a.term !== null)
                    return new Date(a.term).getTime() - new Date(b.term).getTime();
            }
            catch (error) {
                console.error('Sort Error"');
            }
        });
        this.data.forEach(function (surgery) {
            try {
                //handles edge case
                if (surgery.cpt)
                    surgery.cpt = surgery.cpt.replace("level,", "level ");
                if (surgery.diagnosisCode)
                    surgery.diagnosisCode = surgery.diagnosisCode.replace("level,", "level ");
                //GROUP
                var newSurgery = new SurgeryGroupItem(surgery);
                var today = new Date();
                var currentDate = new Date();
                // this.dates.push(currentDate.toLocaleDateString());
                // if (new Date(surgery.term) != currentDate) {
                currentDate = new Date(surgery.term);
                //   let newGroup: SurgeryGroup = new SurgeryGroup(currentDate);
                //   currentSurgeries = newGroup.surgeries;
                //   this.groupedSurgeries.push(newGroup);
                // }
                // currentSurgeries.push(newSurgery);
                newSurgery.occurs = "past";
                if (currentDate.toLocaleDateString() === today.toLocaleDateString()) {
                    newSurgery.occurs = "today";
                    _this.todaySurgeries.push(newSurgery);
                }
                else if (currentDate > new Date()) {
                    newSurgery.occurs = "future";
                    _this.futureSurgeries.push(newSurgery);
                }
                else
                    _this.pastSurgeries.push(newSurgery);
                if (surgery.preferenceCardName)
                    _this.metrics.cards.push(surgery.preferenceCardName.trim());
                if (surgery.speciality)
                    _this.metrics.speciality.push(surgery.speciality.trim());
                if (surgery.admissionStatus)
                    _this.metrics.admissionStatus.push(surgery.admissionStatus.trim());
                if (surgery.patient)
                    _this.metrics.surgeryType.push(surgery.patient.trim());
                if (surgery.cpt) {
                    var qt = surgery.cpt
                        .split(",")
                        .filter(function (w) {
                        return !!w.replace(/^[\s\uFEFF\xA0]+|[\s\uFEFF\xA0]+$/g, "").trim()
                            .length;
                    });
                    qt.forEach(function (code) {
                        _this.metrics.cptCodes.push(code.replace(/^[\s\uFEFF\xA0]+|[\s\uFEFF\xA0]+$/g, ""));
                    });
                }
                if (surgery.diagnosisCode) {
                    var qt = surgery.diagnosisCode
                        .split(",")
                        .filter(function (w) {
                        return !!w.replace(/^[\s\uFEFF\xA0]+|[\s\uFEFF\xA0]+$/g, "").trim()
                            .length;
                    });
                    qt.forEach(function (code) {
                        _this.metrics.diagnosisCodes.push(code.replace(/^[\s\uFEFF\xA0]+|[\s\uFEFF\xA0]+$/g, ""));
                    });
                }
            }
            catch (e) {
                console.error("Error in surgery", 'Surgery', surgery, e.toString());
            }
        });
        this.calculateMetrics();
        // this.reduceGroup();
    };
    DataSurgeryStore.prototype.uniqueMetrics = function (arr) {
        var result = arr.reduce(function (acc, curr) {
            if (typeof acc[curr] === "undefined") {
                acc[curr] = 1;
            }
            else {
                acc[curr] += 1;
            }
            return acc;
        }, {});
        return this.getArray(result);
    };
    DataSurgeryStore.prototype.getArray = function (arr) {
        var r = [];
        for (var key in arr) {
            if (arr.hasOwnProperty(key)) {
                //   let i: IMetric = { count: arr[key], name: key };
                r.push({ count: arr[key], name: key });
            }
        }
        return r;
    };
    DataSurgeryStore.prototype.countMetrics = function (arr) {
        return arr.filter(function (item, i, ar) {
            // tslint:disable-next-line:no-unused-expression
            (function (item) { return !!item.trim().length; });
            return ar.indexOf(item) === i;
        });
    };
    DataSurgeryStore.prototype.calculateMetrics = function () {
        console.log("Processing Surgery Metrics");
        // this.groupedSurgeries = this.groupedSurgeries.sort(
        //   (a: SurgeryGroup, b: SurgeryGroup) => {
        //     return a.realDate.getDate() - b.realDate.getDate();
        //   }
        // );
        this.metrics.uniqueDiag = this.uniqueMetrics(this.metrics.diagnosisCodes);
        this.metrics.diagnosisCodes = this.countMetrics(this.metrics.diagnosisCodes);
        this.metrics.uniqueCpt = this.uniqueMetrics(this.metrics.cptCodes);
        this.metrics.cptCodes = this.countMetrics(this.metrics.cptCodes);
        //ADDMISSIONS
        this.metrics.uniqueAdmissionStatus = this.uniqueMetrics(this.metrics.admissionStatus);
        this.metrics.admissionStatus = this.countMetrics(this.metrics.admissionStatus);
        //CARDS
        this.metrics.uniqueCards = this.uniqueMetrics(this.metrics.cards);
        this.metrics.cards = this.countMetrics(this.metrics.cards);
        //SPECIALITY
        this.metrics.uniqueSpeciality = this.uniqueMetrics(this.metrics.speciality);
        this.metrics.speciality = this.countMetrics(this.metrics.speciality);
        //SURGERY TYPE
        this.metrics.uniqueSurgeryType = this.uniqueMetrics(this.metrics.surgeryType);
        this.metrics.surgeryType = this.countMetrics(this.metrics.surgeryType);
        this.metrics.future = this.futureSurgeries.length;
        this.metrics.past = this.pastSurgeries.length;
        this.metrics.today = this.todaySurgeries.length;
        //get rid of empty future groups.
    };
    DataSurgeryStore.prototype.reduceGroup = function () {
        console.group("Reduce Surgery Group");
        console.log("Old Surgery group count", this.groupedSurgeries.length);
        var newGroups = [];
        this.groupedSurgeries.forEach(function (group) {
            if (group.surgeries.length > 0)
                newGroups.push(group);
        });
        this.groupedSurgeries = newGroups;
        console.log("New Surgery group count", this.groupedSurgeries.length);
        console.groupEnd();
    };
    DataSurgeryStore.prototype.findSugeryById = function (id) {
        var surg = this.futureSurgeries.find(function (s) { return s.surgery.surgeryId === id; });
        if (!surg)
            surg = this.todaySurgeries.find(function (s) { return s.surgery.surgeryId === id; });
        if (!surg)
            surg = this.pastSurgeries.find(function (s) { return s.surgery.surgeryId === id; });
        return surg;
    };
    return DataSurgeryStore;
}());
