    StatsPage.prototype.ionViewDidLoad = function () {
        var _this = this;
        this.auth.storage.get("statfilter").then(function (stats) {
            if (!stats) {
                _this.statFilter = new StatFilter();
                _this.auth.storage.set("statfilter", _this.statFilter);
            }
            else
                _this.statFilter = stats;
        });
        this.metricsSelected = this.metrics[0];
        this.refreshStats();
        this.allStats();
    };
    StatsPage.prototype.refreshStats = function () {
        var _this = this;
        var clone = JSON.parse(JSON.stringify(this.chartData));
        var cloneLab = JSON.parse(JSON.stringify(this.chartLabels));
        console.group("Refresh Stats");
        // this.chartData[0].data=[];
        var index = -1;
        var index2 = -1;
        // this.chartData=new Array<ChartData>();
        var data = [];
        this.yrs.forEach(function (y) {
            index2++;
            var cd = new ChartData();
            index = -1;
            cloneLab.forEach(function (_t) {
                index++;
                var d = moment(y + "-" + (index + 1) + "-01", "YYYY-MM-DD");
                var som = moment(y + "-" + (index + 1) + "-" + moment(d).daysInMonth(), "YYYY-MM-DD");
                var rng = moment.range(d, som);
                var model = new DataSurgeryStore();
                //   this.chartData = new ChartData();
                model.data = _this.surgerySvc.model.data.filter(function (o) {
                    if (o.term) {
                        var when = moment(o.term);
                        return when.within(rng);
                    }
                });
                cd.data.push(model.data.length);
            });
            cd.label = 'Surgeries ' + y;
            clone[index2] = cd;
        });
        this.chartData = clone;
        console.groupEnd();
    };
