    StatsPage.prototype.presentFilter = function () {
        var _this = this;
        var modal = this.modalCtrl.create(StatFilterPage, this.statFilter);
        modal.present();
        this.log.view('Open Filter');
        modal.onWillDismiss(function (data) {
            if (data) {
                //  this.ga.trackEvent('Statistics','Filter Set','Filter Date 1',);
                _this.log.event('Set Filter', _this.statFilter);
                _this.auth.storage.set("statfilter", _this.statFilter);
                _this.statFilter = data;
                _this.startDate = _this.statFilter.sd;
                _this.endDate = _this.statFilter.ed;
                _this.updateTitle();
                _this.refreshStats();
            }
        });
    };
    StatsPage.prototype.updateTitle = function () {
        this.chartTitle = moment(this.statFilter.sd).format("MMM YYYY") + " to " + moment(this.statFilter.ed).format("MMM YYYY") + ", completed: " + this
            .statFilter.completed + ", Cancelled: " + this.statFilter.cancelled + ".";
    };
    StatsPage.prototype.getLabelArray = function (arr) {
        var labs = [];
        if (this.metricsSelected === "Admission Status") {
            this.surgerySvc.model.metrics.uniqueAdmissionStatus.forEach(function (s) {
                if (labs.indexOf(s.name) === -1)
                    labs.push(s.name);
            });
            var labss = arr.filter(function (o) { return o.admissionStatus; });
        }
        if (this.metricsSelected === "Preference Cards") {
            this.surgerySvc.model.metrics.uniqueCards.forEach(function (s) {
                if (labs.indexOf(s.name) === -1)
                    labs.push(s.name);
            });
        }
        if (this.metricsSelected === "CPT Codes") {
            this.surgerySvc.model.metrics.uniqueCpt.forEach(function (s) {
                if (labs.indexOf(s.name) === -1)
                    labs.push(s.name);
            });
        }
        if (this.metricsSelected === "Diagnosis Codes") {
            this.surgerySvc.model.metrics.uniqueDiag.forEach(function (s) {
                if (labs.indexOf(s.name) === -1)
                    labs.push(s.name);
            });
        }
        if (this.metricsSelected === "Specialties") {
            this.surgerySvc.model.metrics.uniqueSpeciality.forEach(function (s) {
                if (labs.indexOf(s.name) === -1)
                    labs.push(s.name);
            });
        }
        if (this.metricsSelected === "Surgeries") {
            this.surgerySvc.model.metrics.uniqueSurgeryType.forEach(function (s) {
                if (labs.indexOf(s.name) === -1)
                    labs.push(s.name);
            });
        }
        return labs;
    };
    StatsPage.prototype.initStats = function () {
        this.allMetricsChart = [];
        this.allMetricsChartLabels = [];
        this.updateChart2(this.surgerySvc.model.metrics.uniqueAdmissionStatus, "Admission Status");
        this.updateChart2(this.surgerySvc.model.metrics.uniqueCards, "Preference Cards");
        this.updateChart2(this.surgerySvc.model.metrics.uniqueCpt, "CPT Codes");
        this.updateChart2(this.surgerySvc.model.metrics.uniqueDiag, "Diagnosis Codes");
        //  this.updateChart2(this.surgerySvc.model.metrics.uniqueSpeciality,"Specialties" );
        this.updateChart2(this.surgerySvc.model.metrics.uniqueSurgeryType, "Surgeries");
    };
